import React from 'react';
import { CartProvider, useCart } from 'react-use-cart';
import Header from '../Header';

function Cart() {
    const {
        isEmpty,
        totalUniqueItems,
        items,
        updateItemQuantity,
        removeItem,
        cartTotal
    } = useCart();

    if (isEmpty) return <h4 className="text-center">Your Bag is empty</h4>;

    return (
        <div>
            <main className="page">
                <section className="shopping-cart dark cart-container">
                    <div className="container">
                        <div className="block-heading">
                            <h2 className="text-primary text-center">Shopping Bag ({totalUniqueItems})</h2>
                            <p className="text-center">Let's go To Checkout</p>
                        </div>
                        <div className="content">
                            <div className="row">
                                <div className="col-md-12 col-lg-8">
                                    {items.map((element) => (
                                        <div className="items" key={element.id}>
                                            <div className="product">
                                                <div className="row">
                                                    <div className="col-md-3">
<img className="img-fluid mx-auto d-block" src={element.images[0]}/>
                                                     </div>
                                                    <div className="col-md-8">
                                                        <div className="info">
                                                            <div className="row">
                                                                <div className="col-md-5 product-name">
                                                                    <div className="product-name">
                                                                        <a href="#">{element.title}</a>
                                                                        <div className="product-info">
                                                                            <div>Category: <span className="value">{element.category}</span></div>
                                                                            <div>Rating: <span className="value">{element.rating}</span></div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <div className="col-md-4 quantity">
                                                                    <label htmlFor="quantity">Quantity:</label>
                                                                    <br />
                                                                    <span>{element.quantity}</span>
                                                                    <br />
                                                                    <button onClick={() => updateItemQuantity(element.id, element.quantity - 1)} className="btn btn-warning">-</button>
                                                                    <button onClick={() => updateItemQuantity(element.id, element.quantity + 1)} className="btn btn-success mx-2">+</button>
                                                                    <button onClick={() => removeItem(element.id)} className="btn btn-danger">x</button>
                                                                </div>
                                                                <div className="col-md-3 price">
                                                                    <span>${Math.round(element.price * element.quantity)}</span>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    ))} 
                                </div>
                                <div className="col-md-12 col-lg-4">
                                    <div className="summary">
                                        <h3>Summary</h3>
                                        <div className="summary-item"><span className="text">Subtotal</span><span className="price">${Math.round(cartTotal)}</span></div>
                                        <div className="summary-item"><span className="text">Discount</span><span className="price">$10</span></div>
                                        <div className="summary-item"><span className="text">Shipping</span><span className="price">$30</span></div>
                                        <div className="summary-item"><span className="text">Total</span><span className="price">${Math.round(cartTotal - 10 + 30)}</span></div>
                                        <button type="button" className="btn btn-primary btn-lg btn-block">Checkout</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        </div>
    );
}

function Allbag() {
    return (
        <CartProvider>
            <Header />
            <Cart />
        </CartProvider>
    );
}

export default Allbag;
