
import React from 'react'
import { NavLink } from 'react-router-dom'
import { CartProvider, useCart } from 'react-use-cart'

function Header() {
    const {totalItems} = useCart ();
  return (
    <CartProvider>
            <header className="header-area header-sticky">
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <nav className="main-nav">
                        <NavLink to="/" className="logo">
                            <img src="assets/images/logo.png"/>
                        </NavLink>
                        <ul className="nav">
                            <li className="scroll-to-section"><NavLink to="/">Home</NavLink></li>
                            <li class="scroll-to-section"><NavLink to="/Properties">Properties</NavLink></li>
                             <li className="submenu">
                                <a href="javascript:;">Pages</a>
                                <ul>
                                    <li><NavLink to="/About">About Us</NavLink></li>
                                    <li><NavLink to="/Products">Products</NavLink></li>
                                    <li><NavLink to="/Single">Single Product</NavLink></li>
                                    <li><NavLink to="/Contact">Contact Us</NavLink></li>
                                </ul>
                            </li>
                            <li className="scroll-to-section">
                                <NavLink to="/Explore">Explore</NavLink></li>
                            <li className="scroll-to-section">
                                <NavLink to="/Shop">Shop</NavLink></li>
                            <li className="scroll-to-section">
                            <NavLink to="/Bag" style={{color: "#000", display: "flex", alignItems: "center"}}>
  <i className="fa-solid fa-bag-shopping" style={{color: "#000", fontSize: "20px", marginRight: "8px"}}>({totalItems})</i>
</NavLink>
</li>
                        </ul>        
                        <a className='menu-trigger'>
                            <span>Menu</span>
                        </a>
                    </nav>
                </div>
            </div>
        </div>
    </header>
    </CartProvider>
  )
}

export default Header