


import React from 'react'
import Header from '../Header'
import Allarea from './Allarea'
import Allsocial from './Allsocial'
import Allsubscribe from './Allsubscribe'
import Banner from '../Home/Banner'
import AllFfooter from './AllFfooter'
import { CartProvider } from 'react-use-cart'

function Allexplore() {
  return (
    <CartProvider>
        <Header/>
        <Banner/>
    <Allarea/>
    <Allsocial/>
    <Allsubscribe/>
    <AllFfooter/>
    </CartProvider>
  )
}

export default Allexplore