

import React from 'react'
import Header from '../Header'
import Singleproduct from './Singleproduct'
import Footer from './Footer'
import { CartProvider } from 'react-use-cart'

function Allsingle() {
  return (
    <CartProvider>
        <Header/>
        <Singleproduct/>
        <Footer/>
    </CartProvider>
  )
}

export default Allsingle