

import React from 'react'
import Header from '../Header'
import Areaproducts from './Areaproducts'
import Footer from './Footer'
import { CartProvider } from 'react-use-cart'

function Allproducts() {
  return (
    <CartProvider>
       <Header/>
       <Areaproducts/>
       <Footer/>
    </CartProvider>
  )
}

export default Allproducts