import React, { Component } from 'react';
import Header from '../Header';
import axios from 'axios';
import { Container, Row } from 'reactstrap';
import { CartProvider } from 'react-use-cart';

class Allproperties extends Component {
  state = {
    persons: []
  };

  componentDidMount() {
    axios.get('/assets/js/Api.json')
      .then((element) => {
        this.setState({
          persons: element.data.products
        });
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
      });
  }

  render() {
    return (
      <CartProvider>
        <Header />
        <div className='section-heading'> 
          <Container>
            <Row>
              {this.state.persons.map((item) => (
                <div className="col-lg-4" key={item.id}>
                  <div className="item">
                    <div className="thumb">
                      <div className="hover-content">
                      </div>
                      <img src={item.images[0]} alt={item.title} /></div>
                    <div className="down-content">
                      <h4>{item.title}</h4>
                      <span>${item.price}</span>
                      <ul className="stars">
                        {[...Array(Math.round(item.rating))].map((i) => (
                          <li key={i}><i className="fa fa-star"></i></li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              ))}
            </Row>
          </Container>
        </div>
      </CartProvider>
    );
  }
}

export default Allproperties;
 