import * as React from "react";
import { createRoot } from "react-dom/client";
import { createBrowserRouter, RouterProvider,} from "react-router-dom";
import App from "./App";
import Allexplore from "./Explore/Allexplore";
import Allabout from "./About/Allabout";
import Allproducts from "./Products/Allproducts";
import Allsingle from "./Single/Allsingle";
import Allcontact from "./Contact/Allcontact";
import Allproperties from "./Properties/Allproperties";
import Allshop from "./Shop/Allshop";
import Allbag from "./Bag/Allbag";
import ScrollToTop from "react-scroll-to-top";
const router = createBrowserRouter([
  {
    path: "/",
    element: <App/> ,
  },
  {
    path: "Explore",
    element: <Allexplore/> ,
  },
  {
    path: "About",
    element: <Allabout/> ,
  },
  {
    path: "Products",
    element: <Allproducts/> ,
  },
  {
    path: "Single",
    element: <Allsingle/> ,
  },
 
  {
    path: "Contact",
    element: <Allcontact/> ,
  },
 
  {
    path: "properties",
    element: <Allproperties/> ,
  },
  {
    path: "Shop",
    element: <Allshop/> ,
  },
  {
    path: "Bag",
    element: <Allbag/> ,
  },
 
  
  
]);

createRoot(document.getElementById("root")).render(
  <div>

    <RouterProvider router={router} />
    <ScrollToTop 
    smooth
    viewBox="0 0 512 512" 
    svgPath="M256 512A256 256 0 1 0 256 0a256 256 0 1 0 0 512zM385 215c9.4 9.4 9.4 24.6 0 33.9s-24.6 9.4-33.9 0l-71-71L280 392c0 13.3-10.7 24-24 24s-24-10.7-24-24l0-214.1-71 71c-9.4 9.4-24.6 9.4-33.9 0s-9.4-24.6 0-33.9L239 103c9.4-9.4 24.6-9.4 33.9 0L385 215z"
    />
  </div>
);
