

import React from 'react'
import Header from '../Header'
import Contactus from './Contactus'
import Subscribe from './Subscribe'
import Footer from './Footer'
import { CartProvider } from 'react-use-cart'

function Allcontact() {
  return (
    <CartProvider>
        <Header/>
        <Contactus/>
        <Subscribe/>
        <Footer/>
    </CartProvider>
  )
}

export default Allcontact